body {
  font-family: 'Muli', sans-serif;
  font-weight: 300;
  display: grid;
  grid-template: ". navbar ." minmax(2em, 1fr) ". container ." auto/minmax(3.6em, 1fr) auto minmax(3.6em, 1fr); }

.clickable {
  cursor: pointer; }

a {
  text-decoration: none;
  color: inherit; }
  a:visited {
    color: inherit; }
  a:hover {
    color: #1F6AFF; }
  a:active {
    color: inherit; }

.navigation {
  grid-area: navbar;
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  margin-top: 1.7em;
  margin-left: -1.4em;
  margin-right: -1.5em;
  margin-bottom: 1.3em;
  max-height: 2em; }
  @media (min-width: 800px) {
    .navigation {
      height: 2em;
      margin-right: 0; } }
  @media (min-width: 800px) {
    .navigation .hamburger {
      display: none; } }
  .navigation .nav-items {
    position: absolute;
    top: 3em;
    left: 50%;
    margin-left: -6em;
    transform: scale(1, 0);
    transform-origin: top;
    transition: transform 150ms ease-in-out; }
    @media (min-width: 800px) {
      .navigation .nav-items {
        position: relative;
        top: unset;
        left: unset;
        margin-left: unset;
        transform: unset;
        transition: unset;
        display: flex;
        flex-direction: row;
        justify-content: flex-start; } }
    .navigation .nav-items .nav-item {
      text-align: center;
      list-style: none;
      margin: 0.4em auto;
      font-weight: 300;
      font-size: 2em;
      position: relative;
      opacity: 0; }
      @media (min-width: 800px) {
        .navigation .nav-items .nav-item {
          opacity: unset;
          margin-right: 1.5em;
          font-size: 1.5em; } }
    .navigation .nav-items .nav-item.active-link::after {
      position: absolute;
      bottom: -0.15em;
      left: 0;
      content: '';
      width: 100%;
      height: 1.5px;
      border-radius: 3px;
      background: #1F6AFF;
      transform: scale(0, 1);
      transform-origin: top;
      transition: transform 300ms ease-in 200ms; }
      @media (min-width: 800px) {
        .navigation .nav-items .nav-item.active-link::after {
          transform: scale(1, 1); } }
  .navigation .github-mark {
    min-width: 35px;
    filter: invert(42%) sepia(70%) saturate(5682%) hue-rotate(213deg) brightness(100%) contrast(103%); }

.navigation.expanded {
  margin-bottom: 15em; }
  @media (min-width: 800px) {
    .navigation.expanded {
      margin-bottom: unset; } }
  .navigation.expanded .nav-items {
    transform: scale(1, 1); }
    .navigation.expanded .nav-items .nav-item {
      opacity: 1;
      transition: opacity 150ms ease-in 250ms; }
    .navigation.expanded .nav-items .nav-item.active-link::after {
      transform: scale(1, 1); }

.flex-spacer {
  flex: 1; }

